import React from 'react';
import { Route, Switch } from 'react-router-dom';
import DashboardPage from './pages/DashboardPage';
import TablesPage from './pages/TablesPage';
import SignInPage from './pages/signin/SignInPage';
import SignInSuccessPage from './pages/signin/SignInSuccessPage';
import SignUpPage from './pages/signup/SignUpPage';
import UploadPage from './pages/upload/UploadPage';
class Routes extends React.Component {
  constructor(props){
    super(props);
    this.state={
      dir : process.env.REACT_APP_DIR,

    }
  }
  
  render() {
    return (
      <Switch>
        <Route path={this.state.dir+'/'} exact component={SignInPage} />
        <Route path={this.state.dir+'/dashboard'} component={DashboardPage} />
        <Route path={this.state.dir+'/upload'} component={UploadPage} />
        <Route path={this.state.dir+'/table'} component={TablesPage} />
        <Route path={this.state.dir+'/signinsuccess'} component={SignInSuccessPage} />
 
        <Route path={this.state.dir+'/signin'} component={SignInPage}/>
        <Route path={this.state.dir+'/signup'} component={SignUpPage}/>
      </Switch>
    );
  }
}

export default Routes;
