import React, {useCallback} from 'react'
import {useDropzone} from 'react-dropzone'
import Paper from '@material-ui/core/Paper';
import { makeStyles } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';
import { Grid } from '@material-ui/core';
import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import axios from 'axios';
const useStyles = makeStyles(theme => ({
    BaseTop:{
      height: "100px",
    },
    backpaper:{
      background: 'rgba(255, 255, 255, 0.5) ',
    },
    paper: {
      //marginTop: theme.spacing(8),
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
    },
    avatar: {
      height: '100px',
      margin: theme.spacing(1),
    },
    form: {
      width: '100%', // Fix IE 11 issue.
      marginTop: theme.spacing(1),
    },
    submit: {
      margin: theme.spacing(3, 0, 2),
    },
  }));
export default function MyDropzone() {
const classes = useStyles();
  const onDrop = useCallback(acceptedFiles => {
    // Do something with the files
    acceptedFiles.map(file=>{
        console.log(file)
        const data = new FormData() 
        data.append('file', file)
        const config = {
            headers: {
                'enctype': 'multipart/form-data'
            }
        };
        axios
        .post(process.env.REACT_APP_API+'upload/', data,config);
    }
    )

  }, [])
  const {getRootProps, getInputProps, isDragActive} = useDropzone({onDrop})

  return (
    <Container style={{background: 'transparent'}} main maxWidth="sm">
        <CssBaseline />
        <div className={classes.BaseTop}/>
        <div {...getRootProps()}>
        <Paper className={classes.backpaper} elevation={0}>
        <Box className={classes.paper} p={6} >
            <input {...getInputProps()} />
            {
                isDragActive ?
                <p>Drop the files here ...</p> :
                <p>Drag 'n' drop some files here, or click to select files</p>
            }
        </Box>
        </Paper>
        </div>
    </Container>
  )
}