import React from 'react';
import SignIn from './signup';
import {  MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import Helmet from 'react-helmet';

const theme = createMuiTheme({
  palette: {
    primary: {
      main: '#384854',
    },
    secondary: {
      main: '#4dba80',
    },
  },
});

const SignUpPage =  () => {
  return (
    <React.Fragment>
      <Helmet>
        <title>IQHi - Sign Up</title>
        <style>{'main { background: url(https://iqhibattery.com/battery-bg.png) no-repeat center fixed; background-size: 100% 100%;}'}</style>
      </Helmet>
      <MuiThemeProvider theme={theme}>
        <SignIn />
      </MuiThemeProvider>
    </React.Fragment>
  )
}

export default SignUpPage;