import React from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Paper from '@material-ui/core/Paper';
import logo from "../../../assets/iqhi.png";
import axios from 'axios';
import { Redirect, useHistory } from 'react-router-dom';
import Divider from '@material-ui/core/Divider';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem'
const useStyles = makeStyles(theme => ({
  BaseTop:{
    height: "100px",
  },
  backpaper:{
    background: 'rgba(255, 255, 255, 0.5) ',
  },
  paper: {
    //marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    height: '100px',
    margin: theme.spacing(1),
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function SignInSuccess(props) {
  const classes = useStyles();

  return (
    <Container style={{background: 'transparent'}} main maxWidth="sm">
      <CssBaseline />
      <div className={classes.BaseTop}/>
      <Paper className={classes.backpaper}>
        <Box className={classes.paper} p={6} >
        <List>
            <ListItem>
            <Typography>
                Welcome Back {localStorage.getItem("firstName")}
            </Typography>
            </ListItem>
            <Divider />
            <ListItem>
                <Link href="https://iqhibattery.com/contact.html">
                    <Button>
                        Contack Us    
                    </Button>
                </Link>
            </ListItem>
            <ListItem>
              <Link href={process.env.REACT_APP_DIR+"/dashboard"}>
                <Button>
                    Demo
                </Button>
                </Link>
            </ListItem>
        </List>
        </Box>
      </Paper>

    </Container>
  );
}