import React from 'react';
import { MDBCard, MDBCardBody, MDBTable, MDBTableBody, MDBTableHead, MDBRow, MDBCol } from 'mdbreact';

const TableSection = () => {
  return (
    <MDBRow className="mb-4">
          <MDBCol md="6">
              <MDBCard>
                  <MDBCardBody>
                    <MDBTable hover>
                      <MDBTableHead color="blue-grey lighten-4">
                        <tr>
                          <th>#</th>
                          <th>File Name</th>
                          <th>Date Created</th>
                          <th>Size</th>
                        </tr>
                      </MDBTableHead>
                      <MDBTableBody>
                        <tr>
                          <td>1</td>
                          <td>Charging_Station.csv</td>
                          <td>2019-10-9</td>
                          <td>5.34MB</td>
                        </tr>
                        <tr>
                          <td>2</td>
                          <td>Charging_Station.xlsx</td>
                          <td>2019-10-10</td>
                          <td>10.11MB</td>
                        </tr>
                        <tr>
                          <td>3</td>
                          <td>Charging_Station.txt</td>
                          <td>2019-10-11</td>
                          <td>98.76MB</td>
                        </tr>
                      </MDBTableBody>
                    </MDBTable>
                  </MDBCardBody>
              </MDBCard>
          </MDBCol>
      </MDBRow>
  )
}

export default TableSection;

