import React from 'react';
import { Link } from 'react-router-dom';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import MenuIcon from '@material-ui/icons/Menu';
import IconButton from '@material-ui/core/IconButton';
import HomeIcon from '@material-ui/icons/Home';
import ExploreIcon from '@material-ui/icons/Explore';
import DashboardIcon from '@material-ui/icons/Dashboard';
import SearchIcon from '@material-ui/icons/Search';
import logo from '../../../assets/iqhi.png';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import Button from '@material-ui/core/Button'
const useStyles = makeStyles(theme =>({
  list: {
    width: 250,
  },
  fullList: {
    width: 'auto',
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(0, 1),
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
  },
}));

export default function TemporaryDrawer() {

  const classes = useStyles();
  const [state, setState] = React.useState({
    left: false,
  });
  const theme = useTheme();
  const toggleDrawer = (side, open) => event => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [side]: open });
  };
  function sign_out(){
    localStorage.clear()
  }

  const sideList = side => (
    <div
      className={classes.list}
      role="presentation"
      onClick={toggleDrawer(side, false)}
      onKeyDown={toggleDrawer(side, false)}
    >
         <div className={classes.drawerHeader}>
          <IconButton onClick={toggleDrawer(side, false)}>
            {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
          </IconButton>
        </div>
        <Divider />
        <a href="#!" className="logo-wrapper waves-effect">
                <img alt="IQHi Logo" className="img-fluid" src={logo}/>
        </a>
        <Divider />
      <List>
        {['Dashboard'].map((text, index) => (
          <Link to={process.env.REACT_APP_DIR+"/dashboard"} key={text}>
            <ListItem button>
            <ListItemIcon>{<HomeIcon />}</ListItemIcon>
            <ListItemText primary={text} />
          </ListItem>
          </Link>
        ))}
      </List>
      <Divider />
      <List>
        {['Upload data'].map((text, index) => (
          <Link to={process.env.REACT_APP_DIR+"/upload"} key={text}>
            <ListItem button>
            <ListItemIcon>{<HomeIcon />}</ListItemIcon>
            <ListItemText primary={text} />
          </ListItem>
          </Link>
        ))}
      </List>
      <Divider />
      <List>
        {localStorage.getItem("firstName")?
        <Button onClick={()=>sign_out()}>Sign Out</Button>
        :['Sign in'].map((text, index) => (
          <Link to={process.env.REACT_APP_DIR+"/signin"} key={text}>
            <ListItem button>
            <ListItemIcon>{<SearchIcon />}</ListItemIcon>
            <ListItemText primary={text} />
          </ListItem>
          </Link>
        ))}
      </List>
      
      <Divider />
    
    </div>
  );


  return (
    <div>
       
       <IconButton
            edge="start"
            className={classes.menuButton}
            color="inherit"
            aria-label="open drawer"
            onClick={toggleDrawer('left', true) }
          >
            <Drawer />
            <MenuIcon />
          </IconButton>
      <Drawer open={state.left} onClose={toggleDrawer('left', false)}>
        {sideList('left')}
      </Drawer>
    </div>
  );
}