import React, { Component } from 'react';
import { MDBCol, MDBCard, MDBCardBody, MDBCardHeader, MDBRow } from 'mdbreact';
import { Line, Doughnut, Radar } from 'react-chartjs-2';

class ChartSection2 extends Component {
    render(){
        const dataLine = {
            labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
            datasets: [
              {
                label: 'Power Consumption (GW)',
                fill: false,
                lineTension: 0.1,
                backgroundColor: 'rgba(75,192,192,0.4)',
                borderColor: 'rgba(75,192,192,1)',
                borderCapStyle: 'butt',
                borderDash: [],
                borderDashOffset: 0.0,
                borderJoinStyle: 'miter',
                pointBorderColor: 'rgba(75,192,192,1)',
                pointBackgroundColor: '#fff',
                pointBorderWidth: 1,
                pointHoverRadius: 5,
                pointHoverBackgroundColor: 'rgba(75,192,192,1)',
                pointHoverBorderColor: 'rgba(220,220,220,1)',
                pointHoverBorderWidth: 2,
                pointRadius: 1,
                pointHitRadius: 10,
                data: [65, 59, 80, 81, 56, 55, 40]
              }
            ]
        };

        const dataRadar = {
            labels: ['Tempture', 'Current', 'Voltage', 'C-Rate', 'Overcharge', 'Overdischarge', 'Heavy Usage'],
            datasets: [
                {
                  label: 'Lithium cobalt oxide (LCO)',
                  backgroundColor: 'rgba(245, 74, 85, 0.5)',
                  data: [65, 59, 80, 81, 56, 55, 40]
                },
                {
                  label: 'Lithium irron phosphate (LFP)', 
                  backgroundColor: 'rgba(90, 173, 246, 0.5)',
                  data: [12, 42, 21, 56, 24, 12, 2]
                },
                {
                  label: 'Lithium Nickel Cobalt Aluminum Oxide (NCA)',
                  backgroundColor: 'rgba(245, 192, 50, 0.5)',
                  data: [2, 23, 54, 76, 54, 23, 5]
                }
              ]
        };

        const dataDoughnut = {
            labels: ["Remain Capacity", "Capacity Loss"],
            datasets: [{
                data: [85, 15],
                backgroundColor: ["#F7464A", "#46BFBD", "#FDB45C", "#949FB1", "#4D5360"],
                hoverBackgroundColor: ["#FF5A5E", "#5AD3D1", "#FFC870", "#A8B3C5", "#616774"]
            }]
        };

        function LineChart(){
            return (
                <MDBCard>
                    <MDBCardHeader>Power Consumption</MDBCardHeader>
                    <MDBCardBody>
                        <Line data={dataLine} options={{responsive: true }} />
                    </MDBCardBody>
                </MDBCard>
            )
        }

        return (
            <MDBRow className="mb-4">
                <MDBCol md="12" lg="4" className="mb-4">
                    <LineChart />
                </MDBCol>
                <MDBCol md="12" lg="4" className="mb-4">
                    <MDBCard className="mb-4">
                    <MDBCardHeader>SOH Influence Factors</MDBCardHeader>
                    <MDBCardBody>
                        <Radar data={dataRadar}  height={300} options={{responsive: true }} />
                    </MDBCardBody>
                    </MDBCard>
                </MDBCol>
                <MDBCol md="12" lg="4" className="mb-4">
                    <MDBCard className="mb-4">
                    <MDBCardHeader>State of health(SOH)</MDBCardHeader>
                    <MDBCardBody >
                        <Doughnut data={dataDoughnut}  height={300} options={{responsive: true }} />
                    </MDBCardBody>
                    </MDBCard>
                </MDBCol>
            </MDBRow>
        )
    }

}

export default ChartSection2;

