import React from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Paper from '@material-ui/core/Paper';
import logo from "../../../assets/iqhi.png";
import axios from 'axios';
import Success from './signupsuccess';
const useStyles = makeStyles(theme => ({
  BaseTop:{
    height: "100px",
  },
  backpaper:{
    background: 'rgba(255, 255, 255, 0.5) ',
  },
  paper: {
    //marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    height: '100px',
    margin: theme.spacing(1),
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function SignIn() {


  const classes = useStyles();

  const [email, setEmail] = React.useState('')
  const [password, setPassword] = React.useState('')
  const [cpassword, setCpassword] = React.useState('')
  const [success, setSuccess] =  React.useState(false)
  const [first, setFirst] = React.useState('')
  const [last, setLast] = React.useState('')
  const [emailhelper, setEmailHelper] = React.useState('')
  function emailHelperText(){
    if(email !== '' && !email.match(/^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i)){
      return "Email is not valid."
    }else{
      if(email === ''){
        return "Please enter the email"
      }else{
        return "email is valid"
      }
    }
  }
  function passwordHelperText(){
    if(password !== '' && password.length < 6){
      return "Minimum password length is 6."
    }else{
      if(email === '')
        return "Please enter the password. Minimum password length is 6."
      else{
        return "password is valid"
      }
    }
  }
  function cpasswordHelperText(){
    if(cpassword !== '' && cpassword !== password){
      return "Comfirm password and password are not the same."
    }else{
      return "Please re-enter the password"
    }
  }

  function submit(){
    axios
      .post(process.env.REACT_APP_API+'new_user/', {first: first, last: last, email:email, password: password})
      .then(res => {
        setSuccess(res.data.success)
        console.log(res.data)
        if(!res.data.success){
          setEmailHelper('email already exists')
        }
      });
  }
  if(success){
    return <Success name={first}/>
  }else{
    return (
      <Container style={{background: 'transparent'}} main maxWidth="sm">
        <CssBaseline />
        <div className={classes.BaseTop}/>
        <Paper className={classes.backpaper}>
        <Box className={classes.paper} p={6} >
          <div>
          <img alt="MDB React Logo" className={classes.avatar} src={logo}/>
          </div>
          <br/>
          <Typography component="h1" variant="h5">
            Welcome. Sign up here
          </Typography>
          <form className={classes.form} noValidate>
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <TextField
                autoComplete="fname"
                name="firstName"
                variant="outlined"
                required
                fullWidth
                id="firstName"
                label="First Name"
                autoFocus
                onChange={(e)=>setFirst(e.target.value)}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                variant="outlined"
                required
                fullWidth
                id="lastName"
                label="Last Name"
                name="lastName"
                autoComplete="lname"
                onChange={(e)=>setLast(e.target.value)}
              />
            </Grid>
              {emailhelper===''?<Grid item xs={12}></Grid>: <Grid item xs={12}><Typography color='error'>{emailhelper}</Typography></Grid>}
            <Grid item xs={12}>
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="email"
              label="Email Address"
              name="email"
              autoComplete="email"
              autoFocus
              error={emailhelper !== ''||(email !== '' && !email.match(/^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i))}
              value={email}
              helperText={emailHelperText()}
              onChange={(e)=>setEmail(e.target.value)}
            />
            </Grid>
            <Grid item xs={12}>
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              name="password"
              label="Password"
              type="password"
              id="password"
              error={password !== '' && password.length < 6}
              helperText = {passwordHelperText()}
              onChange={(e)=>setPassword(e.target.value)}
            />
            </Grid>
            <Grid item xs={12}>
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              name="comfirm password"
              label="Comfirm Password"
              type="password"
              id="cpassword"
              helperText = {cpasswordHelperText()}
              error={(cpassword.length >0 && cpassword !== password)}
              onChange={(e)=>setCpassword(e.target.value)}
            />
            </Grid>
            <FormControlLabel
              control={<Checkbox value="remember" color="primary" />}
              label="Remember me"
            />
            <Button
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submit}
              onClick={()=>submit()}
              disabled={(first ==='' || last === ''||(email === ''||!email.match(/^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i) )|| password.length <6 || cpassword === '')||(cpassword.length >0 && cpassword !== password)}
            >
              Submit
            </Button>
            <Grid container>
              <Grid item xs>
                <Link href="#" variant="body2">
                  Forgot password?
                </Link>
              </Grid>
              <Grid item>
                <Link href="/signin" variant="body2">
                  {"Aready have an account? Sign in"}
                </Link>
              </Grid>
            </Grid>
            </Grid>
          </form>
        </Box>
        </Paper>
      </Container>
    );
  }

}