import React, { Component } from 'react';
import { Line} from 'react-chartjs-2';

import { withStyles } from '@material-ui/core/styles';
import { green } from '@material-ui/core/colors';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import Favorite from '@material-ui/icons/Favorite';
import FavoriteBorder from '@material-ui/icons/FavoriteBorder';
import axios from 'axios'
const RedCheckbox = withStyles({
    root: {
      color: 'rgba(245, 74, 85, 0.5)',
      '&$checked': {
        color: 'rgba(245, 74, 85, 0.5 )',
      },
    },
    checked: {},
})(props => <Checkbox color="default" {...props} />);

const BlueCheckbox = withStyles({
    root: {
      color: 'rgba(90, 173, 246, 0.5)',
      '&$checked': {
        color: 'rgba(90, 173, 246, 0.5)',
      },
    },
    checked: {},
})(props => <Checkbox color="default" {...props} />);

const YellowCheckbox = withStyles({
    root: {
      color: 'rgba(245, 192, 50, 0.5)',
      '&$checked': {
        color: 'rgba(245, 192, 50, 0.5)',
      },
    },
    checked: {},
})(props => <Checkbox color="default" {...props} />);

const VoltageCheckbox = withStyles({
    root: {
      color: 'rgba(139,195,74 ,0.5)',
    },
    checked: {},
})(props => <Checkbox color="default" {...props} />);





class ChartSection1 extends Component {
    constructor(props){
        super(props);
        this.state = {
            labelA: "Charge_Capacity(Ah)",
            labelB: "Discharge_Capacity(Ah)",
            labelC: "Charge_Energy(Wh)",
            labelD: "Discharge_Energy(Wh)",
            checkA: true,
            checkB: true,
            checkC: true,
            checkD: true,
            dataA: [12, 39, 3, 50, 2, 32, 84],
            dataB: [56, 24, 5, 16, 45, 24, 8],
            dataC: [12, 25, 54, 3, 15, 44, 3],
            dataD: [10, 19, 18, 25, 3, 6, 40],
            labels: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul'],
        }
    }
    componentDidMount() {
        this.getData()
    }

    getData = ()=>{
        axios
        .get(process.env.REACT_APP_API+'dashboard_data/')
        .then(res => {
            if(res.data.success){
                let data = res.data
                
              this.setState({
                  labels: data.labels,
                  dataA: data[this.state.labelA],
                  dataB: data[this.state.labelB],
                  dataC: data[this.state.labelC],
                  dataD: data[this.state.labelD],
              })
            }
        });
    }
    CheckboxLabels=()=> {
      
        return (
          <FormGroup row>
            <FormControlLabel
                control={
                <RedCheckbox
                    checked={this.state.checkA}
                    onChange={()=>this.setState({checkA: !this.state.checkA})}
                    value="checkedA"
                />
                }
                label={this.state.labelA}
            />
            <FormControlLabel
                control={
                <BlueCheckbox
                    checked={this.state.checkB}
                    onChange={()=>this.setState({checkB: !this.state.checkB})}
                    value="checkedB"
                />
                }
                label={this.state.labelB}
            />
            <FormControlLabel
                control={
                <YellowCheckbox
                    checked={this.state.checkC}
                    onChange={()=>this.setState({checkC: !this.state.checkC})}
                    value="checkedC"
                />
                }
                label={this.state.labelC}
            />
            <FormControlLabel
                control={
                <VoltageCheckbox
                    checked={this.state.checkD}
                    onChange={()=>this.setState({checkD: !this.state.checkD})}
                    value="voltage"
                />
                }
                label={this.state.labelD}
            />
          </FormGroup>
        );
      }

      dataset = ()=>{
            let data = []
            if(this.state.checkA)
                data.push({
                    label: this.state.labelA,
                    data: this.state.dataA,
                    borderColor: 'rgba(245, 74, 85, 0.5)',
                    pointBorderColor: 'rgba(245, 74, 85, 1)',
                    pointBackgroundColor: 'rgba(245, 74, 85, 1)',
                    pointBorderWidth: 1,
                    pointHoverRadius: 5,
                    fill: false,
                    borderWidth: 3
                })
            if(this.state.checkB)
                data.push({
                    label: this.state.labelB,
                    data: this.state.dataB,
                    borderColor: 'rgba(90, 173, 246, 0.5)',
                    pointBorderColor: 'rgba(90, 173, 246, 1)',
                    pointBackgroundColor: 'rgba(90, 173, 246, 1)',
                    pointBorderWidth: 1,
                    pointHoverRadius: 5,
                    fill: false,
                    borderWidth: 3
                })
            if(this.state.checkC)
                data.push({
                    label: this.state.labelC,
                    data: this.state.dataC,
                    borderColor: 'rgba(245, 192, 50, 0.5)',
                    pointBorderColor: 'rgba(245, 192, 50, 1)',
                    pointBackgroundColor: 'rgba(245, 192, 50, 1)',
                    pointBorderWidth: 1,
                    pointHoverRadius: 5,
                    fill: false,
                    borderWidth: 3
                })
            if(this.state.checkD)
                data.push({
                    label: this.state.labelD,
                    data: this.state.dataD,
                    borderColor: 'rgba(139,195,74 ,0.5)',
                    pointBorderColor: 'rgba(139,195,74 ,1)',
                    pointBackgroundColor: 'rgba(139,195,74 ,1)',
                    pointBorderWidth: 1,
                    pointHoverRadius: 5,
                    fill: false,
                    borderWidth: 3
                })
            return data
      }
    render(){
        const dataBar = {
            labels:this.state.labels,
            datasets: this.dataset()
        };

        const barChartOptions = {
            responsive: true,
            maintainAspectRatio: false,
            scales: {
            xAxes: [{
                barPercentage: 1,
                gridLines: {
                display: true,
                color: 'rgba(0, 0, 0, 0.1)'
                }
            }],
            yAxes: [{
                gridLines: {
                display: true,
                color: 'rgba(0, 0, 0, 0.1)'
                },
                ticks: {
                beginAtZero: true
                }
            }]
            }
        }

        return (
            <div>
                {this.CheckboxLabels()}
            
                <div>
                    <Line data={dataBar} height={500} options={barChartOptions} />
                </div>
            </div>
        )
    }
}

export default ChartSection1;

