import React from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Paper from '@material-ui/core/Paper';
import logo from "../../../assets/iqhi.png";
import axios from 'axios';
import { Redirect, useHistory } from 'react-router-dom';

const useStyles = makeStyles(theme => ({
  BaseTop:{
    height: "100px",
  },
  backpaper:{
    background: 'rgba(255, 255, 255, 0.5) ',
  },
  paper: {
    //marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    height: '100px',
    margin: theme.spacing(1),
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function SignIn(props) {
  const classes = useStyles();

  const [email, setEmail] = React.useState('')
  const [password, setPassword] = React.useState('')
  const [success, setSuccess] = React.useState(false)
  let history = useHistory()
  function submit(){
    axios
      .post(process.env.REACT_APP_API+'signin/', {email:email, password: password})
      .then(res => {
          if(res.data.success){
            localStorage.setItem("firstName", res.data.first)
            localStorage.setItem("lastName", res.data.last)
            history.push(process.env.REACT_APP_DIR+"/dashboard")
            window.location.reload(false);
          }
      });
  }
  return (
    <Container style={{background: 'transparent'}} main maxWidth="sm">
      <CssBaseline />
      <div className={classes.BaseTop}/>
      <Paper className={classes.backpaper}>
      <Box className={classes.paper} p={6} >
        <div>
            <img alt="IQHi Logo" className={classes.avatar} src={logo}/>
        </div>
        <br/>
        <Typography component="h1" variant="h5">
          Welcome Back. Sign in here
        </Typography>
        <form className={classes.form} noValidate>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="email"
            label="Email Address"
            name="email"
            autoComplete="email"
            autoFocus
            onChange={(e)=> setEmail(e.target.value)}
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="current-password"
            onChange={(e)=> setPassword(e.target.value)}
          />
          <FormControlLabel
            control={<Checkbox value="remember" color="primary" />}
            label="Remember me"
          />
          <Button
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
            disabled={password ==='' || email === ''}
            onClick={()=>submit()}
          >
            Sign In
          </Button>
          <Grid container>
            <Grid item xs>
              <Link href="#" variant="body2">
                Forgot password?
              </Link>
            </Grid>
            <Grid item>
              <Link href={process.env.REACT_APP_DIR+"/signup"} variant="body2">
                {"Don't have an account? Sign Up"}
              </Link>
            </Grid>
          </Grid>
        </form>
      </Box>
      </Paper>

    </Container>
  );
}